.contacts {
  margin: 0;
  padding: 0;
}

.contacts__wrapper {
  margin: 0;
  padding: 26px 20px;
  display: grid;
  grid-template-columns: repeat(2, 140px);
}

.contacts__slogan {
  margin: 0;
  padding: 0;
  width: 140px;
  color: #111111;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}

.contacts__address {
  margin: 0;
  padding: 0;
  width: 140px;
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

.contacts__image {
  margin: 0;
  padding: 0;
  width: 320px;
  height: 362px;
  object-fit: contain;
}


@media (min-width: $tablet-width) {
  .contacts__wrapper {
    padding: 60px 70px;
    width: 768px;
    grid-template-columns: 163px 157px;
    justify-content: space-between;
  }

  .contacts__slogan {
    width: 163px;
    font-size: 20px;
    line-height: 26px;
  }

  .contacts__address {
    width: 157px;
    text-align: right;
    font-size: 16px;
  }
}

@media (min-width: $desktop-width) {
  .contacts__wrapper {
    margin-top: 95px;
    margin-left: 110px;
    margin-bottom: 106px;
    padding: 60px 78px;
    width: 565px;
  }

  .contacts__address {
    text-align: left;
  }
}
